.LeftContainer{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    width: 60%;
    color: rgb(235 251 255);
}

.LeftContainer-top{
width: 100%;
display: flex;
justify-content: space-between;
}

.LeftContainer-top .unit{
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    border: none;
    background-color: rgba(94, 94, 94, 0.5);
    color: rgb(0, 0, 0);
    margin-top: 1rem;
    cursor: pointer;
    font-weight: bolder;
    margin-left: 1rem;
}


.unitActive{
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    border: rgb(235 251 255) solid 2px;
    background-color: rgba(94, 94, 94, 0.5);
    color: rgb(235 251 255);
    margin-top: 1rem;
    cursor: pointer;
    font-weight: bolder;
    margin-left: 1rem;
}

.LeftContainer-top .lang{
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    border: none;
    background-color: rgba(94, 94, 94, 0.5);
    color: rgb(0, 0, 0);
    margin-top: 1rem;
    cursor: pointer;
    font-weight: bolder;
    margin-right: 1rem;
}

.langActive{
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    border: rgb(235 251 255) solid 2px;
    background-color: rgba(94, 94, 94, 0.5);
    color: rgb(235 251 255);
    margin-top: 1rem;
    cursor: pointer;
    font-weight: bolder;
    margin-right: 1rem;
}

.LeftContainer-bottom{
    display: flex;
    margin-bottom: 5rem;
}

.LeftContainer-bottom__info{
    display: flex;
    align-items: center;
    width: 100%;
    margin-left: 4rem;
}

.LeftContainer-bottom__info #temp{
    font-size: 5rem;
    font-weight:500;
}

.LeftContainer-bottom__info .city{
    line-height: 1px;
}

#cityName{
    font-size: 2rem;
    font-weight: 500;
}

.LeftContainer-bottom__info .weather{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    
    margin-left: 1rem;
}

.LeftContainer-bottom__info .weather .weatherIcon{
    width: 60px;
    height: 50px;
}

@media screen and (max-width: 759px){

.LeftContainer{
    width: 100%;
}

.LeftContainer-bottom{
    width: 100%;
}

.LeftContainer-bottom__info{
    min-width: 100%;
    display: flex;
    justify-content: center;
    margin: 0;
    flex-wrap: wrap;
}

}