.RightContainer{
    color: rgb(244 237 231);
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family:'Georgia', 'Arial' ,'sans-serif';
    width: 40%;
    justify-content: space-between;
    background-color: rgba(94, 94, 94, 0.15); 
    backdrop-filter: blur(8px);
}

.serach-location{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 90%;
    margin-top: 1rem;
}

.serach-location input{
    background: none;
    outline: none;
    border: none;
    border-bottom: solid rgba(255, 255, 255, 0.486) 1px;
    color: rgba(255, 255, 255, 0.61);
    padding-left: 0.5rem;
    height: 2rem;
    width: 50%;
    font-size: 1rem;
}

.serach-location input::placeholder{
    color: rgba(255, 255, 255, 0.61);
}

.serach-location i{
    cursor: pointer;
}

.RightContainer-weather{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    width: 90%;
}

.RightContainer-weather h6{
    font-size: 1rem;
    text-align: left;
    width: 90%;
    margin-left: 1.5rem;
    color: rgb(235 251 255);
    margin-top: 3rem;
}

.RightContainer-weather__details{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.RightContainer-weather__details div{
    width: 85%;
    display: flex;
    justify-content: space-between;
}

@media screen and (max-width: 759px){
.RightContainer{
    width: 100%;
    height: 100%;
}

}